@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

//@import "preloader";
html {
  // Блокировать скролла
  &.css-scroll-fixed:not(.open-only-mob) {
    @include scroll-blocked;
  }

  // Не блокировать скролл, если есть класс open-only-mob
  &.css-scroll-fixed.open-only-mob {
    @include media("<tab") {
      @include scroll-blocked;
    }
  }
}

/*------ TYPOGRAPHIC STYLES ------*/
.page {
  overflow: auto;
  height: 100%;
}

body {
  font: 16px/30px 'helvetica-neue-cyr-light', Arial, Helvetica, sans-serif;
  height: 100%;
  color: #4c4c4c;
  min-width: 320px;
}

body,
body > .mCustomScrollBox > .mCSB_container {
  background: #fff url(../../img/bg-page.png) 50% 0;
}

body.about-page,
body.about-page > .mCustomScrollBox > .mCSB_container {
  background-attachment: fixed;
}

html.no-scroll,
body.preloader-show {
  width: 100% !important;
  height: 100% !important;
  max-width: 100% !important;
  max-height: 100% !important;
  margin: 0 !important;
  padding: 0 !important;
  overflow: hidden !important;
}

body .no-js {
  overflow: auto;
  max-height: none;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
  margin: 0;
  color: #333;
}

h1 {
  font-family: 'rotondac-bold', Arial, Helvetica, sans-serif;
  font-size: 40px;
  line-height: 50px;
  color: #00abc7;
  padding: 0 0 60px;
  overflow: hidden;
}

.page-title {
  padding: 0 30px;
  text-align: center;
}

h2 {
  font-family: 'rotondac-bold', Arial, Helvetica, sans-serif;
  font-size: 24px;
  line-height: 36px;
  padding: 0 0 30px;
}

h3 {
  font-family: 'helvetica-neue-cyr-bold', Arial, Helvetica, sans-serif;
  font-size: 14px;
  line-height: 30px;
  text-transform: uppercase;
  color: #1a1a1a;
  padding: 0 0 30px;
}

h4 {
  font-family: 'helvetica-neue-cyr-light-italic', Arial, Helvetica, sans-serif;
  font-size: 16px;
  line-height: 30px;
  color: #808080;
  padding: 0 0 30px;
}

h5 {
  font-size: 14px;
  line-height: 16px;
  font-weight: 100;
  padding: 0 0 5px;
}

h6 {
  font-size: 12px;
  line-height: 14px;
  font-weight: 100;
  padding: 0 0 5px;
}

p {
  padding: 0 0 30px;
  margin: 0;
}

hr {
  line-height: 1px;
  height: 0;
  border: none;
  background: none;
  border-bottom: 2px solid #55c7da;
  margin-bottom: 40px;
}

.user-content hr {
  margin-right: -80px;
}

a {
  color: #00abc7;
  outline: none;
  text-decoration: underline;
  cursor: default;
}

a:link {
  cursor: pointer;
}

.desktop a:hover {
  text-decoration: none;
}

a[href^="tel"],
.desktop a[href^="tel"]:hover {
  text-decoration: none !important;
  color: inherit;
}

a[href^="javascript:"] {
  cursor: default;
}

img {
  border: none;
  vertical-align: top;
  max-width: 100%;
  height: auto;
}

b, strong, th {
  font-family: 'helvetica-neue-cyr-bold', Arial, Helvetica, sans-serif;
  font-weight: normal;
}

i, em {
  font-family: 'helvetica-neue-cyr-light-italic', Arial, Helvetica, sans-serif;
  font-style: normal;
}

i b,
i strong,
em b,
em strong,
b i,
b em,
strong i,
strong em {
  font-family: 'helvetica-neue-cyr-bold-italic', Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-style: normal;
}

.nobr {
  white-space: nowrap;
}

.clear {
  clear: both;
}

