/*btn*/
[class|="btn"],
[class*=" btn-"],
input[type="submit"],
input[type="button"],
input[type="reset"],
button {
  display: inline-block;
  vertical-align: top;
  position: relative;
  text-align: center;
  text-decoration: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

[class|="btn"] input[type="submit"],
[class*=" btn-"] input[type="submit"],
[class|="btn"] input[type="button"],
[class*=" btn-"] input[type="button"],
[class|="btn"] input[type="reset"],
[class*=" btn-"] input[type="reset"] {
  background: url("../../img/empty.png") repeat;
  cursor: pointer;
  display: block;
  height: 100%;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
  border: 0;
  opacity: 0;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
}

/*btn-default*/
.btn-default,
input[type="submit"],
input[type="button"],
input[type="reset"],
button {
  font-family: 'helvetica-neue-cyr-bold', Arial, Helvetica, sans-serif;
  font-size: 14px;
  line-height: 30px;
  color: #1a1a1a;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  padding: 10px 34px;
  border: 1px solid transparent;
  background: #fcd105;
  border-radius: 25px;
  -webkit-transition-property: color;
  transition-property: color;
  -webkit-transition-duration: 0.2s;
  transition-duration: 0.2s;
  position: relative;
  box-sizing: border-box;
}

.desktop .btn-default:hover,
.desktop input[type="submit"]:hover,
.desktop input[type="button"]:hover,
.desktop input[type="reset"]:hover,
.desktop button:hover {
  color: #00abc7;
}

.btn-default:active {
  bottom: -1px;
}

/*btn-default end*/

/*btn close*/
.btn-close {
  position: absolute;
  right: 30px;
  top: 30px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  text-indent: -9999px;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
}

.btn-close:before,
.btn-close:after {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  margin: -3px 0 0;
  height: 6px;
  width: 30px;
  border-radius: 5px;
  background: #00abc7;
}

.btn-close:before {
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.btn-close:after {
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

/*btn close end*/
/*btn end*/

/*USER CONTENT*/
.bg-content {
  background: #fff;
  box-shadow: 0 5px 40px rgba(0, 0, 0, 0.12);
}

.user-content p + h2,
.user-content ul + h2,
.user-content ol + h2,
.user-content p + h3,
.user-content ul + h3,
.user-content ol + h3,
.user-content p + h4,
.user-content ul + h4,
.user-content ol + h4,
.user-content p + h5,
.user-content ul + h5,
.user-content ol + h5 {
  padding-top: 20px;
}

/*list*/
.user-content ul, .user-content ol {
  margin: 0;
  padding: 0 0 20px;
  list-style: none;
}

.user-content ol {
  counter-reset: section;
}

.user-content ul li, .user-content ol li {
  padding: 12px 0 0 20px;
  position: relative;
}

.user-content ol > li {
  counter-increment: section;
  height: 1%;
  overflow: hidden;
  padding-left: 0;
}

.user-content ul li:first-child, .user-content ol > li:first-child {
  padding-top: 0;
}

.user-content ul > li:before,
.user-content ol ul > li:first-child:before {
  content: '';
  position: absolute;
  left: 0;
  top: 24px;
  height: 0;
  width: 0;
  border: 3px solid #00abc7;
  border-radius: 50%;
  box-sizing: border-box;
}

.user-content ul > li:first-child:before {
  top: 12px;
}

.user-content ol > li:before {
  display: inline-block;
  text-align: left;
  color: #00abc7;
  min-width: 18px;
  content: counter(section) ".";
  padding: 0 5px 0 0;
}

.user-content ul ul,
.user-content ul ol,
.user-content ol > li > ul {
  padding-bottom: 0;
  padding-left: 0;
}

.user-content ol > li > ul {
  padding-left: 23px;
}

.user-content ul ul > li:before,
.user-content ul ul > li:first-child:before,
.user-content ol ul ul > li:before,
.user-content ol ul ul > li:first-child:before {
  height: 0;
  width: 8px;
  border-width: 1px 0;
  border-radius: 0;
  top: 26px;
}

.user-content ul ul li:first-child,
.user-content ul ol li:first-child,
.user-content ol ol li:first-child,
.user-content ol ul li:first-child {
  padding-top: 12px;
}

.user-content ul ul ul > li:before,
.user-content ul ul ul > li:first-child:before,
.user-content ul ol ul > li:before,
.user-content ul ol ul > li:first-child:before {
  height: 6px;
  width: 6px;
  top: 24px;
  border-width: 2px;
  border-radius: 50%;
}

/*list end*/

