  border: solid #55c7da;
  border-width: 0 0 2px;
  padding: 75px 80px 75px 0;
  margin: 0 -80px 0 0;
  position: relative;
}

.user-content blockquote:before,
.user-content blockquote:after {
  content: '';
  position: absolute;
}

.user-content blockquote:before {
  left: 110px;
  top: 0;
  right: 0;
