/*elements form style*/
input[type="text"],
input[type="password"],
input[type="email"],
input[type="search"],
textarea {
  display: block;
  color: #808080;
  font: 16px/30px 'helvetica-neue-cyr-light', Arial, Helvetica, sans-serif;
  letter-spacing: normal;
  height: 60px;
  width: 100%;
  padding: 14px 30px;
  background: #fff;
  box-shadow: none;
  border: 1px solid #54c7d9;
  border-radius: 3px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  box-sizing: border-box;
  margin: 0 0 20px;
}

.input-wrap input[type="text"],
.input-wrap input[type="password"],
.input-wrap input[type="email"],
.input-wrap input[type="search"],
.input-wrap textarea {
  margin: 0;
}

textarea {
  line-height: 20px;
  padding-top: 19px;
  padding-bottom: 19px;
  min-height: 140px;
  height: 140px;
  resize: vertical;
}

/*placeholder style*/
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: #808080;
  transition: 0.2s;
  -webkit-transition: 0.2s;
}

input:-moz-placeholder,
textarea:-moz-placeholder {
  color: #808080;
  transition: 0.2s;
  -webkit-transition: 0.2s;
}

input::-moz-placeholder,
textarea::-moz-placeholder {
  color: #808080;
  opacity: 1;
  transition: 0.2s;
  -webkit-transition: 0.2s;
}

input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  color: #808080;
  opacity: 1;
  transition: 0.2s;
  -webkit-transition: 0.2s;
}

input.placeholder,
textarea.placeholder {
  color: #808080;
  opacity: 1;
  transition: 0.2s;
  -webkit-transition: 0.2s;
}

/*placeholder style end*/
/*placeholder style hover*/
.desktop input:hover::-webkit-input-placeholder,
.desktop textarea:hover::-webkit-input-placeholder {
