}

.desktop input:hover::-moz-placeholder,
.desktop textarea:hover::-moz-placeholder {
  color: #333;
}

.desktop input:hover:-ms-input-placeholder,
.desktop textarea:hover:-ms-input-placeholder {
  color: #333;
}

.desktop input.placeholder:hover,
.desktop textarea.placeholder:hover {
  color: #333;
}

/*placeholder style hover end*/
/*placeholder hidden*/
