/*checkbox*/
input[type="checkbox"] {
  width: 16px;
  height: 16px;
  padding: 0;
  margin: 0;
}

input[type="checkbox"] {
  display: none
}

.checkbox-list,
.user-content .checkbox-list,
.radio-list,
.user-content .radio-list {
  padding: 0;
  margin: 0;
  list-style: none;
  line-height: 24px;
}

.checkbox-list li,
.user-content .checkbox-list li,
.user-content .checkbox-list li:first-child,
.radio-list li,
.user-content .radio-list li,
.user-content .radio-list li:first-child {
  position: relative;
  padding: 0 0 20px;
}

.checkbox-list li:before,
.user-content .checkbox-list li:before,
.radio-list li:before,
.user-content .radio-list li:before {
  content: none;
}

input[type="checkbox"] + label,
input[type="radio"] + label {
  display: inline-block;
  vertical-align: top;
  font: 14px/24px Arial, Helvetica, sans-serif;
  color: #1a1a1a;
  text-transform: none;
  cursor: pointer;
  position: relative;
}

input[type="checkbox"] + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 24px;
  width: 24px;
  border-radius: 3px;
  background: #54c7d9;
}

input[type="checkbox"] + label:after {
  content: '';
  position: absolute;
  left: 2px;
  top: 2px;
  height: 20px;
  width: 20px;
  border-radius: 2px;
  background: #fff;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.15);
  -webkit-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-duration: 0.2s;
  transition-duration: 0.2s;
}

input[type="checkbox"] + label > span,
input[type="radio"] + label > span {
  display: inline-block;
  vertical-align: top;
  position: relative;
  padding: 2px 0 2px 40px;
  z-index: 2;
}

input[type="checkbox"] + label > span:before,
input[type="checkbox"] + label > span:after {
  content: '';
  position: absolute;
  height: 2px;
  width: 0;
  background: #fff;
  -webkit-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-duration: 0.2s;
  transition-duration: 0.2s;
  -webkit-transition-timing-function: linear;
  transition-timing-function: linear;
}

input[type="checkbox"] + label > span:before {
  transform: rotate(45deg);
  transform-origin: 0 0.6em;
  left:0;
  top: 8px;
}

input[type="checkbox"] + label > span:after {
  transform: rotate(-50deg);
  transform-origin: 0 1.0em;
  left: 20px;
  top: 11px;
}

input[type="checkbox"]:checked + label > span:before {
  width: 0.6em;
  -webkit-transition-property: width;
  transition-property: width;
  -webkit-transition-duration: 0.15s;
  transition-duration: 0.15s;
  -webkit-transition-delay: 0.2s;
  transition-delay: 0.2s;
}

input[type="checkbox"]:checked + label > span:after {
  width: 1.0em;
  -webkit-transition-property: width;
  transition-property: width;
  -webkit-transition-duration: 0.15s;
  transition-duration: 0.15s;
  -webkit-transition-delay: 0.4s;
  transition-delay: 0.4s;
}

input[type="checkbox"]:checked + label:after {
  opacity: 0;
}

input[type="checkbox"]:disabled + label {
  color: #bdbdbd;
}

input[type="checkbox"]:disabled + label:before {
  background: #ececec;
}

input[type="checkbox"]:disabled + label > span:before,
input[type="checkbox"]:disabled + label > span:after {
  background: #b9b9b9;
}

/*checkbox end*/
/*radio*/
input[type="radio"] {
  float: left;
  height: 15px;
  width: 16px;
  margin: 0;
  padding: 0;
}

input[type="radio"] {
  display: none
}

input[type="radio"] + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 24px;
  width: 24px;
  border-radius: 50px;
  background: #54c7d9;
}

input[type="radio"] + label:after {
  content: '';
  position: absolute;
  left: 2px;
  top: 2px;
  height: 20px;
  width: 20px;
  border-radius: 50px;
  background: #fff;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.15);
  -webkit-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-duration: 0.2s;
  transition-duration: 0.2s;
}

input[type="radio"] + label > span:after {
  content: '';
  width: 12px;
  height: 12px;
  position: absolute;
  top: 6px;
  left: 6px;
  border-radius: 50%;
  overflow: hidden;
  background: #54c7d9;

  -webkit-animation: bounceout 0.3s;
  -webkit-animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;

  animation: bounceout 0.3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;

}

input[type="radio"]:checked + label > span:after {
  -webkit-animation: bounce 0.3s;
  -webkit-animation-iteration-count: 1;

  animation: bounce 0.3s;
  animation-iteration-count: 1;
}

@-webkit-keyframes bounce {
  0% {
    -webkit-transform: scale(0.8);
    opacity: 0.4
  }
  50% {
    -webkit-transform: scale(1.4);
    opacity: 1
  }
  100% {
    -webkit-transform: scale(1);
    opacity: 1
  }
}

@keyframes bounce {
  0% {
    transform: scale(0.8);
    opacity: 0.4
  }
  50% {
    transform: scale(1.4);
    opacity: 1
  }
  100% {
    transform: scale(1);
    opacity: 1
  }
}

@-webkit-keyframes bounceout {
  0% {
    -webkit-transform: scale(0);
    opacity: 1;
  }
  50% {
    -webkit-transform: scale(1.4);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(0);
    opacity: 0.4;
  }
}

@keyframes bounceout {
  0% {
    transform: scale(0);
    opacity: 1;
  }
  50% {
    transform: scale(1.4);
    opacity: 1;
  }
  100% {
    transform: scale(0);
    opacity: 0.4;
  }
}

input[type="radio"]:disabled + label {
  color: #bdbdbd;
}

input[type="radio"]:disabled + label:before {
  background: #d6d6d6;
}

input[type="radio"]:disabled + label:after {
  box-shadow: none;
  background: #ececec;
}

input[type="radio"]:disabled + label > span:after {
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.25);
  background: #ececec;
}

