/* button menu */
.btn-menu {
  position: absolute;
  right: 30px;
  top: 50%;
  height: 30px;
  width: 30px;
  margin-top: -15px;
  text-indent: -9999px;
  overflow: hidden;

  display: none;

  @include media('<mob-s') {
    right: 20px;
  }

  &:active {
    transform: scale(0.9, 0.9);
  }

  // button menu lines
  .btn-lines {
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    height: 0;

    // all lines
    & > span,
    &::before,
    &::after {
      display: block;
      width: 30px;
      height: 4px;
      background: #fff;
      border-radius: 2px;
      position: absolute;
      left: 0;
      top: 0;
      will-change: transform, opacity;
      transition-property: transform, opacity;
      transition-duration: 0.4s;
      transition-timing-function: cubic-bezier(1, 0, 0, 1);
      transform: translateZ(0) scale(1, 1);
      backface-visibility: hidden;
    }

    &::before,
    &::after {
      content: '';
      position: absolute;
      top: -2px;
      opacity: 0;
      z-index: 2;
    }

    & > span {
      // first line
      &:nth-child(1) {
        top: -12px;
      }

      // second line
      &:nth-child(2) {
        top: -2px;
      }

      // third line
      &:nth-child(3) {
        top: 8px;
      }
    }
  }

  // Active (close view)
  &.active {
    .btn-lines {
      &::before {
        opacity: 1;
        transform: rotate(-45deg);
      }

      &::after {
        opacity: 1;
        transform: rotate(45deg);
      }

      & > span {
        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(3) {
          opacity: 0;
        }
      }
    }
  }
}

/* button menu end */

/* menu opener */
.menu-opener {
  box-sizing: border-box;
  position: absolute;
  left: 30px;
  top: 50%;
  width: 62px;
  padding: 12px 10px 6px;
  margin: -30px 0 0;
  text-decoration: none;
  color: $link-color;
  border-radius: 5px;
  line-height: 1;
  z-index: 5;
  background-color: $base-color-alt;
  transition: background-color $animation-speed, box-shadow $animation-speed;
  box-shadow: 0 0 0 lighten($base-color-alt, 20%);

  @include media('<mob-s') {
    right: 20px;
  }

  //:hover
  &:hover {
    background-color: lighten($base-color-alt, 20%);
    box-shadow: 0 0 20px lighten($base-color-alt, 20%);
  }

  // :active
  &:active {
    transform: scale(0.9, 0.9);
    transition-duration: 0s;
  }

  // menu opener lines
  .btn-lines {
    width: 100%;
    height: 20px;
    margin: 0 auto 5px;

    // all lines
    & > span,
    &::before,
    &::after {
      display: block;
      position: absolute;
      left: 0;
      height: 2px;
      width: 100%;
      border-radius: 2px;
      background: currentColor;
      will-change: transform, opacity;
      transition-property: transform, opacity;
      transition-duration: 0.4s;
      transition-timing-function: cubic-bezier(1, 0, 0, 1);
      transform: translateZ(0);
      backface-visibility: hidden;
    }

    // close lines
    &::before,
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      opacity: 0;
      width: 30px;
      margin: -1px -15px;
    }

    & > span {
      // first line
      &:nth-child(1) {
        top: 0;
      }

      // second line
      &:nth-child(2) {
        top: 50%;
        margin: -1px 0;
      }

      // third line
      &:nth-child(3) {
        bottom: 0;
      }
    }
  }

  // Text
  & > span {
    display: block;
    text-align: center;
    font-family: 'rotondac-bold', Arial, Helvetica, sans-serif;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    white-space: nowrap;
  }

  // Active (close view)
  &.menu_open {
    .btn-lines {
      &::before {
        opacity: 1;
        transform: rotate(-45deg);
      }

      &::after {
        opacity: 1;
        transform: rotate(45deg);
      }

      & > span {
        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(3) {
          opacity: 0;
        }
      }
    }
  }
}

/* menu opener end */

