  height: 100%;
  box-sizing: border-box;

  .js &:not(.is-loaded) {
    opacity: 0;
    pointer-events: none;
    overflow: hidden;
  }

  // slider slide
  // ----------------------------------------------------
  .promo-slider-el {
    display: block;
    text-decoration: none;
    color: inherit;
    height: 100%;
    background-color: $base-color;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: cover;
  }

  .promo-slider-el__title {
    display: none;
  }


  // slider arrows
  // ----------------------------------------------------
  .swiper-button-prev,
  .swiper-button-next {
    position: absolute;
    top: 50%;
    width: 80px;
    height: 80px;
    margin-top: -40px;
    background: #ec1b24;
    opacity: 0.75;
    -webkit-transition: opacity $animation-speed;
    transition: opacity $animation-speed;

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      height: 14px;
      width: 37px;
      margin: -7px 0 0 -18px;
      background: url(../../img/swiper-arrows.png) no-repeat 0 0;
      -webkit-background-size: 100%;
      background-size: 100%;
    }

    &::after {
      content: none;
    }

    .desktop &:hover {
      opacity: 1;
    }
  }

  .swiper-button-prev {
    left: 0;

    &::before {
      background-position: 0 0;
    }
  }

  .swiper-button-next {
    right: 0;

    &::before {
      background-position: 0 -34px;
    }
  }


  // slider pagination
  // ----------------------------------------------------

  $swp-size: 0.4rem 4rem;
  $swp-int: round-float(nth($swp-size, 1) / 2, 1) round-float(nth($swp-size, 2) / 2, 1);
  $swp-space: 1rem;
  // ------ mob -----------------------------------------
  $swp-size--mob: 0.8rem 2rem;
  $swp-int--mob: round-float(nth($swp-size--mob, 1) / 2, 1) round-float(nth($swp-size--mob, 2) / 2, 1);
  $swp-space--mob: 0.8rem;

  .swiper-pagination {
    pointer-events: none;
    line-height: 0;
    text-align: left;
    padding: 0 0.4rem;
    z-index: 15;

    display: none;
  }

  .swiper-pagination-bullet {
    position: relative;
    display: inline-block;
    vertical-align: top;
    height: nth($swp-size, 1);
    width: nth($swp-size, 2);
    border-radius: 0;
    border: none;
    margin: $swp-space;
    opacity: 1;
    pointer-events: auto;
    background: none;
    outline: none;

    @include media('<mob') {
      height: nth($swp-size--mob, 1);
      width: nth($swp-size--mob, 2);
      margin: $swp-space--mob;
    }

    &::before,
    &::after {
      content: "";
      display: block;
      position: absolute;
      //left: 50%;
      //top: 50%;
      left: 0;
      top: 0;
    }

    &::before {
      height: nth($swp-size, 1);
      width: nth($swp-size, 2);
      border-radius: 0;
      background-color: #d8d8d8;

      transition: opacity $animation-speed, transform $animation-speed;

      opacity: 0.2;

      @include media('<mob') {
        height: nth($swp-size--mob, 1);
        width: nth($swp-size--mob, 2);
      }
    }

    &::after {
      height: nth($swp-size, 1);
      width: nth($swp-size, 2);
      border-radius: 0;
      background-color: $base-color;
      transition: opacity $animation-speed, transform $animation-speed;

      opacity: 0;

      @include media('<mob') {
        height: nth($swp-size--mob, 1);
        width: nth($swp-size--mob, 2);
      }
    }

    // hover
    &:not(.swiper-pagination-bullet-active):hover {
      &::before {
        opacity: 1;
      }
    }

    &.swiper-pagination-bullet-active {
      cursor: default;

      &::after {
        opacity: 1;
        //$scale: 1, 1;
        //transform: scale($scale);
        //transform: scale3d($scale, 1);
      }
    }
  }

  .swiper-container-horizontal {
    & > .swiper-pagination-bullets {
      bottom: 0;

      .swiper-pagination-bullet {
        margin: $swp-space;

        @include media('<mob') {
          margin: $swp-space--mob;
        }
      }
    }
  }
}

