}

/*tape slider scrollbar*/
.tape-slider__scrollbar {
  position: absolute;
  left: 8px;
  right: 8px;
  top: 100%;
  height: 12px;
  line-height: 0;
  padding: 4px 0;
  box-sizing: border-box;
}

.tape-slider__scrollbar .tape-slider__handle {
  width: 100px;
  height: 4px;
  border-radius: 4px;
  background: #00abc7;
