  position: absolute;
  top: -9px;
  left: 0;
  width: 100%;
  height: 20px;
}

/*tape slider scrollbar end*/

/*tape slider frame*/
.tape-slider__frame {
  overflow: visible !important;
}

.tape-slider__holder {
  height: 100%;
}

.tape-slider__holder:before,
.tape-slider__holder:after {
  content: "";
  display: table;
}

.tape-slider__holder:after {
  clear: both;
}

.tape-slider__holder .products__item {
  display: block;
  float: left;
  width: 280px;
  height: 100%;
}

.tape-slider__holder .products__content {
  /*height: 164px;*/
  overflow: hidden;
}

.tape-slider__holder .products__subtitle {
  height: 60px;
}

/*.tape-slider__holder .products__title,*/
.tape-slider__holder .products__param dd {
  display: block;
  overflow: hidden;
  position: relative;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/*tape slider end*/
.tape-slider__arr {
  position: absolute;
  top: 0;
  height: 100%;
  width: 60px;
  z-index: 5;
  cursor: pointer;
  -webkit-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
}

.mobile .tape-slider__arr {
  display: none !important;
}

.tape-slider__arr.disabled {
  opacity: 0;
  pointer-events: none;
}

.tape-slider__prev {
  left: 0;
  background: #fff;
  background: url(data:image/svg+xml;base64,);
  background: -moz-linear-gradient(left, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0.5) 75%, rgba(255, 255, 255, 0) 100%);
  background: -webkit-linear-gradient(left, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0.5) 75%, rgba(255, 255, 255, 0) 100%);
  background: linear-gradient(to right, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0.5) 75%, rgba(255, 255, 255, 0) 100%);
}

.tape-slider__next {
  right: 0;
  background: #fff;
  background: url(data:image/svg+xml;base64,);
  background: -moz-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.5) 25%, rgba(255, 255, 255, 1) 100%);
  background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.5) 25%, rgba(255, 255, 255, 1) 100%);
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.5) 25%, rgba(255, 255, 255, 1) 100%);
}

.tape-slider__arr:before,
.tape-slider__arr:after {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  height: 6px;
  width: 20px;
  margin-left: -13px;
  border-radius: 5px;
  background: #00abc7;
}

.tape-slider__arr:before {
  margin-top: -8px;
}

.tape-slider__arr:after {
  margin-top: 2px;
}

.tape-slider__prev:before {
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.tape-slider__prev:after {
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.tape-slider__next:before {
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.tape-slider__next:after {
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

