.filter-more-options {
  display: block;
  padding: 0 25px;
  font-family: 'rotondac-regular', Arial, Helvetica, sans-serif;
  line-height: 30px;
  color: #00abc7;
  text-align: center;
  text-decoration: none;
  position: relative;
  min-width: 120px;
  -webkit-transition-property: background;
  transition-property: background;
  -webkit-transition-duration: 0.2s;
  transition-duration: 0.2s;

  font-size: 0;
  letter-spacing: -4px;
}

.filter-more-options:before {
  content: '';
  display: inline-block;
  min-height: 100px;
  vertical-align: middle;
}

.desktop .filters__group:hover .filter-more-options,
