}

.user-content .filters-drop__list li:before,
.user-content .filters-drop__list li:first-child:before {
  content: none;
}

.filters-drop__list a {
  /*display: inline-block;
  vertical-align: top;*/
  /*?*/
  display: block;
  min-height: 30px;
  padding: 0 0 0 40px;
  position: relative;
  color: #1a1a1a;
  text-decoration: none;
  -webkit-transition-property: color;
  transition-property: color;
  -webkit-transition-duration: 0.2s;
  transition-duration: 0.2s;
}

.desktop .filters-drop__list a:hover,
.filters-drop__list a.is-checked {
  color: #00abc7;
}

.filters-drop__list a:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 30px;
  width: 30px;
  border-radius: 3px;
  border: 1px solid #333;
  box-sizing: border-box;
  -webkit-transition-property: border;
  transition-property: border;
  -webkit-transition-duration: 0.2s;
  transition-duration: 0.2s;
}

.desktop .filters-drop__list a:hover:before,
.filters-drop__list a.is-checked:before {
  border-color: #00abc7;
}

.filters-drop__list a:after {
  content: '';
  position: absolute;
  left: 7px;
  top: 8px;
  width: 16px;
  height: 14px;
  opacity: 0;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
  background: url(../../img/sprite-icons.png) 0 -250px no-repeat;
  -webkit-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-duration: 0.2s;
  transition-duration: 0.2s;
}

.filters-drop__list a.disabled:after {
  background-position: 0 -284px;
  width: 16px;
  height: 14px;
}

.filters-drop__list a.is-checked:after {
  opacity: 1;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
}

.filters-drop__list a > [class*="depict"] {
  float: left;
  margin-right: 6px;
  width: 30px;
  height: 30px;
}

.filters-drop__list a > span {
  display: block;
  overflow: hidden;
  padding: 3px 0;
}

